    .h3 Edad antigua

    .row.mb-3
      .col-lg-7
        p La edad antigua inicia aproximadamente en el ao 4.000 a.C. Esta parte de la historia se caracteriza por varios avances que al igual que en el periodo neoltico son fundamentales en el desarrollo y trascender de la humanidad. Si bien es cierto el hombre desde el paleoltico ya dejaba su huella en las cuevas de Altamira, la escritura no se haba desarrollado hasta este momento. El desarrollo de los pueblos y la necesidad de comunicarse hacen que se manifiesten otras formas de comunicacin diferentes a la expresin oral. Es en este periodo que muchos pueblos empiezan a implementar diversos smbolos que representan el lenguaje hablado, los primeros indicios de escritura, se encuentran en civilizaciones como los egipcios, griegos y los chinos.
