    h4.mb-4 Egipto  
    .row.justify-content-center.mb-5
      .col-lg-7
        p Civilizacin antigua que se origin en la parte oriental de frica del Norte, a lo largo del cause medio y alto del ro Nilo, en lo que hoy es el estado moderno de Egipto. La civilizacin se unific en torno al ao 3.150 a. C., con la unificacin poltica del Alto y el Bajo Egipto en el reinado del primer faran, considerado el representante de Dios en la tierra y cuyo poder era supremo.
        p El xito de la antigua civilizacin egipcia est relacionado en gran parte por su capacidad de adaptabilidad a las condiciones del valle del ro Nilo; su ubicacin geogrfica influye mucho en su desarrollo artstico, cultural y social. Es una sociedad fluvial, la inundacin previsible y riego controlado a los campos frtiles, permiti cultivos prsperos, fuentes de riqueza.
        .cajon.color-acento-botones.p-4.mb-4
          p.m-0 Tuvo un gran desarrollo en las ciencias, el comercio, la religin y las artes; el 
            strong arte Antiguo Egipto 
            | comprende los estilos de pintura, escultura, artesana y arquitectura desarrollados desde el ao 5000 a. C hasta el 300 d. C., se caracteriza por un sentido de orden: 
            strong las lneas claras y sencillas, combinadas con formas simples y reas planas de color. 
            | Los artesanos utilizaban las lneas perpendiculares, verticales y horizontales, formando una red y dando las proporciones correctas en su trabajo. El arte refleja la situacin social, religiosa y poltica (enciclopedia, 2010).
        p El arte egipcio guarda un gran simbolismo en cuanto a temas funerarios y religiosos; el misticismo, las supersticiones sobre el ms all, y la devocin a los dioses, se expresa en todo su esplendor. 
