    TabsC.color-primario.mb-5
      .py-3.py-md-4(titulo="Arquitectura")
        .row
          .col-md-5.mb-3.mb-md-0
            figure
              img(src='@/assets/curso/egipto-arquitectura.svg', alt='Arquitectura egipcia')
          .col-md-1
          .col-md-6
            .h2.mb-4 Arquitectura
            p Los egipcios utilizaron principalmente la piedra en sus construcciones, por su fcil acceso y durabilidad. Sus edificaciones eran monumentales, con bases fuertes y muros gruesos.
            p Debido a la escasez de materiales, predominan en construccin el adobe (ladrillos de barro) y la piedra, fundamentalmente piedra caliza, tambin piedra arenisca y granito en cantidades enormes. Del imperio antiguo en adelante, la piedra fue reservada generalmente para tumbas y templos, mientras que los ladrillos fueron utilizados en viviendas, incluso en los palacios reales, fortalezas, muros de los recintos de los templos y de las ciudades, y para edificios subsidiarios en los complejos de los templos.
            p El clima de Egipto, afortunadamente seco y clido preserv, como la aldea de Deir el-Medina, la ciudad de Kahun del imperio medio, o las fortalezas en Buhen y Mirgissa. Por otra parte, muchos templos y tumbas han perdurado porque fueron construidos de piedra, o asentadas en tierras altas, no afectadas por las inundaciones del ro Nilo. Muchas antiguas ciudades egipcias han desaparecido porque estaban situadas cerca de las zonas cultivables del valle del Nilo, que eran inundadas peridicamente con el lodo del ro y se elevaron lentamente durante milenios; o porque los ladrillos de adobe, con que se construyeron, fueron utilizados como fertilizante por los campesinos. Otros edificios son inaccesibles, ya que las nuevas construcciones fueron erigidas sobre las antiguas (Castro, 2014).
      .py-3.py-md-4(titulo="Escritura")
        .row
          .col-md-5.mb-3.mb-md-0
            figure
              img(src='@/assets/curso/egipto-escritura.svg', alt='Escritura egipcia')  
          .col-md-1
          .col-md-6
            .h2.mb-4 Escritura
            p Los egipcios fueron grandes poseedores de conocimientos, durante siglos su escritura fue casi que indescifrable hasta que apareci la piedra Rosetta, la cual ayud a descifrar algunos smbolos. Los egipcios no solo utilizaban los pictogramas, tambin tenan dentro de su escritura fonemas con un valor sonoro. Dichos jeroglficos podan ser ledos en varias direcciones, de izquierda a derecha, de derecha a izquierda, de abajo a arriba y viceversa.
            p La piedra Rosetta es descubierta en 1779 cerca al brazo del Nilo, en Rosetta de donde toma su nombre, un oficial francs hace el descubrimiento y observan tres tipos de escritura diferente. La piedra es llevada a Londres en donde se empiezan a realizar estudios por varios eruditos, que terminan descifrando finalmente su contenido.


    h4.mb-4 Grecia  
    .row.justify-content-center.mb-4
      .col-lg-7
        p La civilizacin es una de las ms importantes, pues ha sido el fundamento del mundo occidental. All naci la democracia. Sin lugar a duda, la cultura griega, es la base de la cultura occidental. Esta se extiende a un perodo de mil aos. Recibi la influencia de otras culturas como la egipcia y la del prximo oriente. Su ubicacin geogrfica en el Mediterrneo hizo que tuviera un intercambio ms dinmico con otras culturas y formas de pensamiento, favorecido por el comercio y la economa. Estaban organizados en ciudades-estado o polis, independientes y enfrentadas usualmente; sin embargo, compartan la misma lengua, la religin que era politesta y se unificaban culturalmente en la Hlade (endnimo con el cual los antiguos griegos identificaban su regin). Atenas fue la protagonista, pero debido al profundo intercambio entre sus ciudades-estado, la demanda artstica se hizo ms variada y menos lujosa, y surgi la competencia. En ese momento, la naturaleza fue sinnimo de armona numrica, basada en la simetra y la proporcin, por ende, transmita belleza. 
        .cajon.color-acento-botones.p-4.mb-4
          p.m-0 
            strong Los griegos 
            | consideraban que eran una raza superior y 
            strong se inclinaban por el perfeccionismo, 
            | para ellos el hombre era el centro de todo, as como la vida; tenan certeza que sus divinidades tenan apariencia humana, pero eran eternos y dotados de poderes. All naci la mitologa, por medio de la cual el hombre se conoca as mismo, explicando racionalmente el mundo, permitiendo el surgimiento de la filosofa, explicando que nada es casual y que el mundo es un todo ordenado y dinmico. Entonces surgieron, la ciencia, para comprender cmo funciona el mundo, se desarrollaron las matemticas, por ende, la fsica y la astronoma, y emerge con mayor esplendor la msica y el arte.
      .col-lg-5
        figure
          img(src='@/assets/curso/grecia.svg', alt='Grecia')      

    p Este auge en Grecia se produjo en tres etapas:   

    .row.mb-4
      .col-lg-4.col-grecia
        .cajon.color-naranja.p-4.mb-4
          .row.mb-3
            .col-8
              h4.mb-0 Arcaica
              p (Siglos VII y VI a. C.)
            .col-4 
              figure
                img(src='@/assets/curso/ico-grecia-arcaica.svg', alt='Grecia arcaica')  
          p Surgimiento de una nueva escritura (adopcin del alfabeto fenicio).
      .col-lg-4.col-grecia
        .cajon.color-naranja.p-4.mb-4
          .row.mb-3
            .col-8
              h4.mb-0 Clsica
              p (Siglos V y IV a.C.)
            .col-4 
              figure
                img(src='@/assets/curso/ico-grecia-clasica.svg', alt='Grecia clsica')  
          p Sinnimo de perfecto y digno de imitar, con logros artsticos importantsimos, poca que termina con la muerte de Alejandro Magno y finalmente la poca. 
      .col-lg-4.col-grecia
        .cajon.color-naranja.p-4.mb-4
          .row.mb-3
            .col-8
              h4.mb-0 Helenstica
              p (Siglos III - I a.C.)
            .col-4 
              figure
                img(src='@/assets/curso/ico-grecia-helenistica.svg', alt='Grecia helenstica')  
          p Se fragmenta Grecia y culmina con la conquista Romana.   

    TabsC.color-primario.mb-5
      .py-3.py-md-4(titulo="La arquitectura griega")
        .row
          .col-md-5.mb-3.mb-md-0
            figure
              img(src='@/assets/curso/griega-arquitectura.svg', alt='La arquitectura griega')
          .col-md-1
          .col-md-6
            .h2.mb-4 La arquitectura griega
            p Constituy el punto de partida de lo que se iba a desarrollar posteriormente en el transcurso de ms de dos mil aos. Al principio utilizaron adobe o madera, que fue reemplazada por la piedra, sobre todo mrmol. Trabajaron el espacio axial y ortogonal, las cubiertas horizontales, los espacios fueron diseados para el hombre en forma inteligente, articulados al igual que un ser viviente, manteniendo perfecta simetra y proporcin. Construyeron santuarios, que eran ciudades sagradas, en los cuales sacerdotes y pitonisas, buscaban la respuesta de los dioses a travs del orculo, se realizan juegos y pruebas deportivas, actividades musicales y teatrales. Las construcciones estaban rodeadas de columnas, idearon tres estilos de rdenes: drico, jnico y corintio. En la cornisa, que es un componente horizontal, levantaron monstruosas grgolas; otro elemento fundamental e importante a destacar son las obras de carcter ldico como lo es el teatro, construidos en laderas de montaas, a gran escala y de forma semicircular. En cuanto a la vivienda urbana, se observa que tenan una planta rectangular con un patio en el centro, hacia el cual daban las estancias. Se puede afirmar, que se mantuvo el orden ortogonal arquitectnica y urbansticamente, dando nacimiento al urbanismo, como pauta que determina cmo distribuir el espacio vital.
      .py-3.py-md-4(titulo="Escultura y pintura")
        .row
          .col-md-5.mb-3.mb-md-0
            figure
              img(src='@/assets/curso/griega-escritura.svg', alt='Escultura y pintura')  
          .col-md-1
          .col-md-6
            .h2.mb-4 Escultura y pintura
            p Los romanos eran monotestas y al conquistar Grecia, destruyeron muchas pinturas, pues los dominados eran politestas. 
              strong La escultura se centr en la perfeccin del cuerpo humano, 
              | usualmente atletas desnudos que haban triunfado en los diversos juegos deportivos de la poca. Tambin elaboraron estatuas de dioses. Utilizaron la piedra caliza y el mrmol; trabajaron el bronce, la madera, el oro, el marfil, piedras de colores y coronas de metal, entre otros.
            p Inicialmente, los cuerpos eran rgidos, a medida que evolucion la escultura, le imprimieron movimiento, se hizo menos rgido el cuerpo, se arque y se evit la verticalidad. Entonces recurrieron a la geometra para llegar a la perfeccin de las medidas corporales. Utilizaron nuevas tcnicas de relieve y el retrato, posteriormente, se inclinaron por esculturas en posiciones ms naturales, alejndose de la geometra y acercndose a la anatoma humana, alcanzando el realismo de la escultura helenstica.
            p Los vasos de cermica que eran utilizados para el uso domstico se adornaban con figuras humanas de frente. Por medio de la tcnica del escorzo, que es un recurso utilizado para dar la sensacin de profundidad, ubicando las figuras en forma oblicua o perpendicular sobre la superficie que se pinta.


    h4.mb-4 Roma  
    .row.justify-content-center.mb-4
      .col-lg-7
        p Hacia el siglo III a. C., Roma inicia la conquista de territorios y empieza a expandir sus dominios, sobre casi toda la Europa de hoy, Asia menor y el norte de frica. La conquista de Grecia continental y por ende las islas del mar Egeo, verdadero centro de la cultura y el arte helenstico, hizo que los romanos fueran influenciados.
        p Roma fue gobernada por los emperadores, tena una slida economa, una misma lengua (el latn), una misma religin (politesta), una moneda nica, y un ordenamiento jurdico que regul las actividades de sus ciudadanos (crearon el derecho). 
          strong Las primeras expresiones de arte romano tomaron forma en el arte de los etruscos, 
          | pueblo asentado en la actual Toscana, que se destac por el arte funerario expresado en pinturas y esculturas, destacando sus terracotas. Se caracterizaba este arte por el naturalismo, la frescura que le imprima a los bustos de bronce (fueron sus creadores). En cuanto a la arquitectura, sus casas eran de adobe, con estructura de madera, revestidos de barro cocido y los templos construidos a base de piedra.
      .col-lg-5
        figure
          img(src='@/assets/curso/roma.svg', alt='Roma') 

    .row.justify-content-center.align-items-center.mb-5.cajon.color-acento-botones
      .col-lg-2.justify-content-center.col-5.px-md-4.p-4
        figure
          img(src='@/assets/curso/casco-romano.svg', alt='Casco Romano')
      .col-lg-9.p-lg-4.px-4
        p Los romanos tenan su propia visin del mundo. Ellos entendan la vida como el cumplimiento de la voluntad de sus dioses, entonces el hombre asignaba un orden en la naturaleza, y se adoraban a los dioses en bsqueda de proteccin. Por ello 
          strong el arte romano 
          | se caracteriz por 
          strong el realismo, 
          | aqu los rostros captaban la fuerza del momento, ellos no buscaban la perfeccin de los griegos. Roma fue una sociedad rgida, en la que coexistan hombres libres, patricios, plebeyos y esclavos. Las esculturas de los emperadores, quienes tenan la categora de divinidad, siempre hacan referencia a importantes hechos histricos. La religin romana tena carcter oficial, se relacionaba con la poltica y recordaba a cada uno el lugar que le corresponda en la sociedad, todo inspirado en el servicio a las divinidades.

    .row.justify-content-center.mb-4
      .col-lg-10
        figure
          img(src='@/assets/curso/coliseo-romano.png', alt='Coliseo romano')  

    p Los romanos crearon el concepto de urbe, para dar nombre a la ciudad, pues las ciudades se erigan conforme a un diseo circular llamado orbis. En el centro resida el poder poltico, cruzado por dos ejes, que significaban la salida y el regreso a las conquistas. Utilizaron piedra, hormign (verdadero aporte romano que permite la arquitectura abovedada) y ladrillo, a veces revestan con mrmol. Construyeron el teatro (influenciado por el teatro griego), el anfiteatro (lugar donde se enfrentaban los gladiadores), el circo (donde se realizaban competencias de carros tirados por caballos y conducidos por aurigas), el acueducto (fueron sus inventores, y construyeron cloacas para conducir las aguas residuales), numerosos puentes y calzadas para que los ciudadanos pudieran comunicarse, termas o baos pblicos para relajarse (calentaban el agua por medio de un horno subterrneo o hipocausto), la baslica (lugar de reunin para realizar transacciones comerciales y de administracin de justicia), templos y viviendas unifamiliares llamadas domus (eran rectangulares, con un patio central y un jardn posterior). Los romanos revestan sus casas con pinturas murales al fresco y el suelo con mosaicos de mrmol, tambin expresaron el arte por medio de retratos, paisajes y bodegones.
    p Queda claro que 
      strong el aporte ms importante de los romanos el arte, se dio en la arquitectura, 
      | pues todas las construcciones aqu mencionadas fueron creacin exclusiva de los romanos, ya que solamente el teatro y el templo tuvieron influencia griega.

    .super-separador
      .separador2


    // 1.2.3.
    .h3.mb-5 Edad media   

    h4 El arte bizantino  

    .row.mb-4
      .col-lg-7
        p La palabra Bizantino corresponde al Imperio romano de oriente, cuya capital era Bizancio, en el perodo comprendido entre el 395 al 1454, ao en que Constantinopla fue tomada por los turcos. El emperador Constantino, pretendi consolidar la unidad poltica, trasladando la capital imperial de Roma a Bizancio que era una ciudad griega, en los lmites entre Europa y Asia. Hoy se conoce como Estambul. Pero en el ao 395, el emperador Teodosio dividi el imperio, separando el mundo latino del griego. Luego de enfrentar la invasin germnica, solamente las provincias de oriente continuaron el legado cultural romano, fundindose con la cultura helenstica. En ese momento el imperio era teocrtico con un solo Dios y una sola autoridad, el emperador, quien estaba encargado de defender y propagar la fe; sin embargo, la divisin del imperio produjo tambin la divisin religiosa, en Oriente eran ortodoxos y en occidente cristianos. Esta situacin produjo tambin cambios en el arte, debido a su ubicacin privilegiada entre Europa y Oriente. 
        p 
          strong La arquitectura bizantina, proviene de la romana adoptando algunos de sus elementos y de la paleocristiana, 
          | porque en los inicios del cristianismo, la baslica o el templo eran muy importantes, entonces la arquitectura se refleja en una planta central con cpula y varias naves, reservando espacios amplios para el clero. Las cpulas de oriente se cubrieron con ladrillo y un sistema de contrarresto. 
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/arte-bizantino.png', alt='El arte bizantino')      

    .row.mb-3
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/arte-bizantino2.png', alt='El arte bizantino')  
      .col-lg-7
        p La pintura en este perodo representa el absoluto influjo de la religin en la vida social y poltica del Imperio Oriental, pero a comienzos del ao 730, bajo la autoridad del emperador Len III, surgi una crisis iconoclasta que dur hasta el siglo IX, cuando prohbe la elaboracin de imgenes religiosas en el Imperio, ya que consider que dichas imgenes conducen a la idolatra, porque en la materia estaba lo maligno y por lo tanto no contribuan en propsito de seguir el camino a lo divino. Por esa razn los mosaicos fueron cubiertos con yeso, y daaron muchas obras religiosas; dentro de los templos los motivos religiosos se reemplazaron por retratos del emperador, escenas de guerra, de carreras de caballos o de caza, vegetales y animales. Pero en 843, los ortodoxos derrotaron a los destructores, luego vino el cisma entre las iglesias de Constantinopla y Roma, entonces las imgenes volvieron a ser admitidas en los templos, pero con un valor sagrado, rindiendo culto a Dios por medio de ellas. Sin embargo, la libertad del artista se limit, debiendo ajustarse a temas antiguos y conservando el estilo clsico. La parte superior de las construcciones religiosas, son una reproduccin de los mundos celestiales, mientras que la tierra corresponda a la zona situada entre el suelo y el arranque de los arcos. 
        
    .row.justify-content-center.align-items-center.mb-5.cajon.color-acento-botones
      .col-lg-2.justify-content-center.col-5.px-md-4.p-4
        figure
          img(src='@/assets/curso/cruz-bizantina.svg', alt='Cruz arte bizantino')
      .col-lg-9.p-lg-4.px-4
        p 
          strong Los temas ms importantes son Cristo, la Virgen Mara, los santos, el juicio final y algunas fiestas cristianas. 
          | Se destacan los mosaicos con colores brillantes, revistieron los muros con teselas de vidrio, oro y plata, por el contrario, la pintura bizantina con sus iconos o imgenes sagradas trabajadas sobre madera con tcnicas propias y utilizando colores opacos; elaboraron iconos, que eran imgenes sagradas pintadas sobre madera, recubiertas de una fina capa de yeso, en la que con un buril se dibujaba y se aplicaban colores al temple.

    // El arte romnico
    h4 El arte romnico 

    .row.mb-3
      .col-lg-7
        p El arte romnico surge en el siglo X y se extiende hasta el XII. En el siglo X, Otn es proclamado emperador del Sacro Imperio Romano Germnico. Este emperador junto con Carlomagno quien gobern en el siglo IX, fij las bases de la Europa feudal caracterizada por el feudo y la dependencia de los vasallos respecto a su seor. En el centro de la propiedad, que era una enorme extensin de tierra, estaba el castillo, la catedral o la abada. El poder dependa de la cantidad de tierras que se poseyeran y en la cspide social estaba el rey. En esa poca la iglesia catlica logr la unidad de Europa; pero la Iglesia estaba manejada por el poder civil, pues los obispos eran nobles a quienes los reyes u otros nobles les entregaban sus dicesis; en correspondencia, la institucin del papado se fortaleci. Los monjes se dedicaron a realizar trabajos manuales, tareas agrcolas y artesanales, convirtiendo los monasterios benedictinos en centros econmicos independientes y en focos culturales de primera importancia, ya que copiaban manuscritos antiguos, realizaron pinturas de miniaturas y redactaron crnicas histricas. Pero los monasterios tambin se implican en el entramado de la vida social y poltica de Europa, lo que conllev inmoralidad y desorden. Por esta razn, en el ao 910, naci la primera Abada de Cluny, fundada por Guillermo el piadoso, 
          strong con la intencin de regresar a una mayor espiritualidad, imponiendo el estilo romnico 
          | en forma uniforme al construir conventos e iglesias y se vuelcan ms en lo mundano que en lo divino, 
          strong poniendo la belleza al servicio de la fe, pues nada era ostentoso ni extremadamente rico para adorar a Dios. 
          | Pero frente a esta abada, surge la orden de Cster, fruto de otra reforma, basada en la austeridad y la pobreza. Esta orden elimin todo lo decorativo, suprimieron pinturas y esculturas, para ellos lo importante era lo estructural, dando origen al arte gtico.
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/arte-romanico.png', alt='El arte romnico')      

    .row.mb-3
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/arte-romanico2.png', alt='El arte romnico')  
      .col-lg-7
        p La iglesia promovi las peregrinaciones, las cuales incentivaron la vida cultural y artstica, ya que fue necesario construir iglesias del estilo romnico, en las que se realizaba el culto a las reliquias, como los cuerpos de los santos y sus objetos personales. Esto requera construcciones arquitectnicas en las que deba reposar el cuerpo del santo, y alojar a los peregrinos y fieles, as como una gran produccin de imgenes en pinturas y esculturas, que an son objeto de devocin. As surgi la ruta de peregrinacin de Santiago de Compostela. 
        
    .row.justify-content-center.mb-5.cajon.color-acento-botones
      .col-lg-3.justify-content-center.col-5.px-md-5.p-4
        figure
          img(src='@/assets/curso/monasterio-romanico.svg', alt='Monasterio romnico')
      .col-lg-9.p-lg-4.px-4
        p En cuanto a 
          strong la arquitectura romnica, se caracteriza por las iglesias en forma de cruz latina, 
          | usualmente tienen una cripta en la que se depositaban los cuerpos de los santos. En esta poca se retomaron materiales y tcnicas de construccin de los romanos, tambin construyeron campanarios (estructura construida para disponer las campanas). La arquitectura tena un significado religioso, por eso la escultura romnica es monumental y estas obras como iban dirigidas a los fieles y peregrinos se ubicaban en las portadas de las iglesias, dentro de ellas y en los conventos. Las imgenes tienen como protagonista a Cristo, utilizaron la decoracin geomtrica, vegetal, humana o animal dispuesta en sentido radial. En las columnas tambin esculpieron estatuas; las representaciones en general eran religiosas, relacionadas con diferentes pasajes de la Biblia, por ejemplo, los cuatro evangelistas, el juicio final y otros. La ubicacin de las imgenes dentro de las iglesias se haca por orden jerrquico y de importancia, por ejemplo, en el norte est el antiguo testamento, el sur es el nuevo y el oeste el juicio final; las pinturas murales se ubicaron en el bside o parte posterior del altar.

    // El arte gtico
    h4 El arte gtico

    .row.mb-3
      .col-lg-7
        p Se inici en el siglo XII, alcanzando su esplendor en los siglos XIII al XV. Los cambios en la sociedad produjeron el desplazamiento de los campos hacia las regiones urbanas, en donde domin la burguesa, clase fundamentalmente comerciante, que propendi por el sentido comunal, la defensa de los intereses de sus habitantes y la autonoma municipal. Por esa razn, al lado del poder religioso representado en las catedrales, se construyeron los ayuntamientos. Los artesanos y los artistas se organizaron en gremios que buscaban evitar la competencia y el monopolio. Los gremios se encargaron de realizar numerosas obras artsticas, siendo la ciudad un polo de desarrollo cultural, sobre todo por el surgimiento de las universidades, que desplazaron a los monasterios. En esa poca, Santo Toms de Aquino afirmaba que la razn precede a la fe, mientras que San Francisco de Ass antepona la fe a la racionalidad. Ambas corrientes coincidan en que 
          strong el hombre haba encontrado a Dios, 
          | pues l tambin habitaba en la tierra, y ya no era necesario buscarlo como se afirmaba en la poca romnica; 
          strong este pensamiento fue la base del estilo artstico gtico. 
          | Como la presencia divina se manifestaba a travs de la luz, en la arquitectura gtica se utiliz el vidrio con tonos preferencialmente azules y rojos, y la piedra en forma de sillares, por cuanto lo estructural tuvo para la poca enorme valor esttico.
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/arte-gotico.png', alt='El arte gtico')      

    .row.mb-3
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/arte-gotico2.png', alt='El arte gtico')       
      .col-lg-7
        p La escultura tambin present un desarrollo importante; sin embargo, la pintura no se destac sino hasta la primera mitad del siglo XIV, especialmente en Italia. Tambin cambi el diseo de las catedrales ms altas y de estructura ntida en el que las diferentes unidades espaciales perdieron independencia en pro de una mayor integracin, procurando que entrara ms luz, por eso los muros se construyen con grandes ventanales. El arco que caracteriz este perodo es el apuntado o tambin llamado ojival, siendo ms esbelto, igualmente se construyeron canales para expulsar el agua lluvia a travs de la boca de grgolas. El arte gtico se caracteriz por ser vertical y abierto; alrededor de las ciudades se construyeron murallas, dos calles principales se cortaban en cruz, en el centro la iglesia y el ayuntamiento. Hacia el siglo XIV, la catedral deja de ser el principal centro y se construyeron casas consistoriales y palacios, iniciando una tendencia con muchos ornamentos, por lo que en el siglo XV se produce el gtico tardo (periodo final). Sin embargo, Italia sigui apegndose a lo clsico, por lo que a principios del siglo XV en Florencia se origin el renacimiento, mientras que el resto de Europa viva la mxima expresin gtica. 
        
    .row.justify-content-center.mb-5.cajon.color-acento-botones
      .col-lg-2.justify-content-center.col-5.px-md-3.p-4
        figure
          img(src='@/assets/curso/catedral-de-milan.svg', alt='Catedral de Miln')
      .col-lg-9.p-lg-4.px-4
        p La escultura gtica tuvo una funcin didctica, ya que la mayora de los fieles eran analfabetas, volvi al naturalismo y la armona del hombre con la naturaleza. 
          strong Las imgenes se tornaron ms reales, semejando movimiento y sentimientos, 
          | expresando el lado humano, apegndose a lo material y a lo individual como medio para llegar a Dios. En los templos la escultura tuvo mayor importancia, porque estaban en decadencia los frescos (llamada tambin pintura mural), pues los muros tenan grandes ventanales. Los retablos hechos de madera se llenaron de imgenes esculpidas o pintadas y reemplazaron las pinturas romnicas.

    .row.mb-4
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/Giotto-di-Bondone.png', alt='Giotto di Bondone')  
      .col-lg-7
        p Hasta el siglo XIV en Italia y en Flandes en el siglo XV, la pintura se desarroll ms tarde que la arquitectura y la escultura. En Italia 
          strong la pintura tena influencia bizantina, 
          | manteniendo un estilo idealista, con curvas delicadas, fondos dorados y cuerpos alargados, conservando la mayor parte de los descubrimientos helensticos que pasaron a la edad media. Pero las demandas de la burguesa adinerada exigan ms obras pictricas realistas, en las que se reprodujera el mundo visible aplicando el naturalismo de los escultores gticos. Pintaron especialmente figuras en un espacio que representaba profundidad. Giotto utiliz el fresco con dominio completo, introdujo la tercera dimensin, lo que consigui a travs del escorzo y el juego entre luces y sombras, y el fondo antes dorado lo sustituy por el cielo y el paisaje. Podemos afirmar que entonces lo divino se acerc ms a lo humano; Giotto se hizo famoso, hecho que era nuevo pues hasta el momento las obras de arte eran annimas y se convirti en el precedente de la perspectiva, que fue la gran conquista del Renacimiento. 

    .row.mb-5
      .col-lg-7
        p En cuanto a Flandes, se produjo una renovacin en la pintura, inclinndose hacia la realidad y partiendo de la observacin, incorporando todos los detalles posibles, utilizando el claroscuro, lo cual hizo ganar en profundidad y volumen a las obras, dando mayor apariencia de vida. Utilizaron el leo cubierto al final con una capa de barniz. Esta tcnica pictrica permite desarrollar el estilo flamenco, pintando imgenes religiosas y el retrato de los burgueses flamencos, quienes preferan pinturas en caballete por ser ms fciles de transportar. En ese momento histrico Italia ya se encaminaba al Renacimiento.
      .col-lg-5
        figure
          img(src='@/assets/curso/arte-gotico3.png', alt='El arte gtico')          



    // 1.2.4
    .h3.mb-5 Edad moderna

    h4 Renacimiento 

    .row.justify-content-center.mb-5.cajon.color-acento-botones
      .col-lg-3.justify-content-center.col-5.px-md-5.p-4
        figure
          img(src='@/assets/curso/renacimiento.svg', alt='Renacimiento')
      .col-lg-9.p-lg-4.px-4
        p Fue el nombre que se le dio a un importante movimiento filosfico y artstico que se produjo en Europa a finales del siglo XIV hasta el siglo XVI y fue una etapa de transicin de la Edad Media a la Era Moderna. Su principal desarrollo se produjo en Italia. Su denominacin se debe a que en esta poca se redescubrieron la cultura greco-romana y la filosofa clsica. Se inspir en la olvidada cultura de la edad media, el racionalismo, la experimentacin, y tambin en 
          strong la exaltacin de la naturaleza humana, pues el hombre fue el centro del universo, 
          | la medida de todas las cosas. En esa etapa se produjeron grandes descubrimientos cientficos en medicina, astronoma, matemticas, geografa y fsica, se buscaron nuevas rutas martimas, se conquistaron nuevos territorios. El Renacimiento naci en Italia, ya que, debido al profuso comercio sobre el Mediterrneo, se expandi a travs del mar hacia otros pases, lo que revivi el intercambio cultural entre oriente y occidente.

    .row.justify-content-center.mb-4
      .col-lg-10
        figure
          img(src='@/assets/curso/renacimiento.png', alt='Renacimiento')      

    p A medida que ms personas aprendan a leer y que los libros se imprimieron y llegaron a sitios geogrficos diversos gracias al invento de la imprenta, creacin de 
      strong Johannes Gutenberg, 
      | la capacidad de raciocinio, se gener una revolucin intelectual en la bsqueda del conocimiento, pues la difusin de textos propenda por la renovacin cultural y por la reinterpretacin de los valores tradicionales greco-romanos. Los cambios se dieron gradualmente y afectaron a toda la sociedad en todos los aspectos. Esa revolucin intelectual, condujo a que muchas personas empezaran a cuestionar el papel de la iglesia catlica, situacin que conllev al surgimiento del protestantismo. Sin embargo, el hombre renacentista no era pagano, solamente tena una prctica religiosa, diferente a pocas anteriores.
    p.mb-5 Durante el Renacimiento, los artistas firmaban sus obras y la burguesa compraba imgenes o estatuas de s mismos, formando parte del hedonismo que caracteriz a la poca. Los artistas eran realistas, porque integraban al hombre con la naturaleza, la creacin y el universo, y dependan econmicamente de mecenas (patrocinio que se otorgaba a los artistas), quienes eran comerciantes, banqueros, aristcratas, incluso Papas.

    .row.mb-4
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/mona-lisa.png', alt='Retrato de Lisa Gherardini')          
      .col-lg-7
        p El auge del Renacimiento fue entre 1490 y 1527, y en cuanto a la arquitectura Filippo di Ser Brunellesco Lapi, fue el ms destacado, tambin fue escultor y orfebre. Utiliz sus grandes conocimientos en matemticas para inventar la perspectiva cnica, utilizando tambin los elementos constructivos de la antigedad. Sus principales obras son la cpula de la catedral de Florencia, la baslica de San Lorenzo, la fachada del hospital de los Inocentes.  Otro destacado representante del Renacimiento fue Michelangelo Buonarroti, quien era arquitecto, escultor y pintor, caracterizado por su perfeccionismo en la escultura y en la pintura (realiz la obra excepcional de la bveda de la capilla Sixtina por imposicin del Papa Julio II), elabor esculturas muy importantes como La Piedad y El David. As mismo, 
          strong Leonardo Da Vinci fue considerado el ltimo hombre del Renacimiento, 
          | fue un gran polmata (persona que tiene conocimiento en diferentes disciplinas), por cuanto era pintor, anatomista, matemtico, arquitecto, cientfico, escritor, paleontlogo, botnico, escultor, filsofo, ingeniero, msico, urbanista e inventor, tuvo creaciones relacionadas con el helicptero, el submarino entre otras. Sus obras ms representativas son La Gioconda y La ltima Cena.

    // El Barroco
    h4 El Barroco

    .row.mb-3
      .col-lg-6
        p Este perodo abarc el siglo XVII y principios del XVIII, y surgi en un perodo lleno de tensiones religiosas entre catlicos y protestantes, y tuvo variaciones en cada pas, conforme a si se trataba de un pas regido por una monarqua absolutista o parlamentaria. Se extendi en Europa occidental y sus colonias americanas recientemente conquistadas. Naci en Italia en el perodo conocido como Seicento, que inicialmente se refera a un estilo artstico recargado y excesivo. El barroco expresaba una sensibilidad desengaada, crtica, compleja y pesimista. El trmino fue utilizado inicialmente para referirse a algo engaoso o recargado en forma despectiva, pero desde el siglo XIX, el trmino se utiliz para referirse no slo al perodo sino a las manifestaciones artsticas que contradijeron los valores del clasicismo, mediante una negacin simblica y artstica de lo clsico, por eso fue femenino, irracional y dionisaco. 
      .col-lg-1
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/barroco.png', alt='El Barroco')      

    .row.justify-content-center.mb-4.cajon.color-acento-botones
      .col-lg-2.justify-content-center.col-5.p-4
        figure
          img(src='@/assets/curso/fuente.svg', alt='Fuente')
      .col-lg-9.p-lg-4.px-4
        p El barroco enfatiz 
          strong lo cotidiano y efmero de la vida, 
          | lo cual produjo una vulgarizacin del imaginario religioso, consider que lo nacional y religioso eran expresiones de poder poltico, por eso se elaboraron obras monumentales y recargadas que propendan por hacer resaltar el poder dominante, pero mediante engaos y artificios. 

    p.mb-5 La pintura fue una expresin artstica del barroco, se distinguen dos vertientes, una 
      strong naturalista 
      | y la otra es el 
      strong clasicismo, 
      | caracterizadas por el realismo superficial, los colores ricos, y el contraste entre las luces y las sombras. Exponentes de este perodo son Rembrandt, Rubens, Velsquez, Poussin, Vermeer y Caravaggio, quien realizaba una minuciosa observacin anatmica con el uso del claroscuro que otorgaba dramatismo a su obra y que lleg a conocerse como tenebrismo. Caravaggio, plasm vvidos momentos y escenas relacionadas con luchas violentas, muerte o tortura.

    TabsA.color-primario.color-custom.mb-5
      .tarjeta.color-primario.color-custom.p-4(titulo="Naturalisa")
        .row
          .col-xl-1
          .col-xl-3
            .p.titulo__template--a.mb-4 Su  estilo se basa en la observacin de la naturaleza
          .col-xl-1
          .col-xl-6.mb-4
            figure
              img(src='@/assets/curso/Naturalisa.png', alt='Naturalisa')
          .col-xl-1    
      .tarjeta.color-primario.color-custom.p-4(titulo="Clasicismo")
        .row
          .col-xl-1
          .col-xl-3
            .p.titulo__template--a.mb-4 Opuesta al naturalismo con un estilo ms realista y racional, predominando el dibujo sobre el color
          .col-xl-1
          .col-xl-6.mb-4
            figure
              img(src='@/assets/curso/Clasicismo.png', alt='Clasicismo')
          .col-xl-1

    .row.mb-5
      .col-lg-7
        p En cuanto a la arquitectura, en el barroco tuvo formas ms dinmicas, con amplio sentido teatral, as como decoracin excesiva y exuberante. Utilizaron las formas cncavas y convexas, los juegos pticos con perspectiva y dieron importancia al urbanismo, pues los gobernantes deseaban transformar los espacios pblicos en ciudades.
        p La escultura barroca estuvo vinculada con la religin y con la arquitectura porque decoraban los espacios pblicos, los palacios y las locaciones religiosas. Eran dinmicas, expresivas y ornamentadas; expresaban pasiones y estaban recargadas de detalles. Esta se expres en dos vertientes, una la gtica y otra la renacentista.  En trminos generales durante el perodo barroco se siguieron trabajando los temas de historia, mitologa y religin, aunque preferan las obras llenas de dramatismo. La Iglesia Catlica prefiri los temas como la inmaculada concepcin, la virgen, la eucarista, los santos, asuntos claves en la disputa del protestantismo.
      .col-lg-5
        figure
          img(src='@/assets/curso/arquitectura-barroca.png', alt='Retrato de Michelangelo Merisi da Caravaggio - Iglesia barroca')      



    // 1.2.5.
    .h3.mb-5 Edad contempornea

    h4 Cubismo

    .row.mb-3
      .col-lg-7
        p Fue un movimiento artstico que prosper entre 1907 y 1924, cuyos creadores fueron Pablo Picasso y Georges Braquese. Ese trmino, a pesar de que fue un movimiento realmente corto, plasm la ruptura definitiva con la pintura tradicional y la norma clsica en la composicin, transformando la perspectiva, la naturaleza y la profundidad.
        p Pas por dos etapas, la primera, el 
          srong cubismo analtico 
          | y la segunda, llamada 
          strong cubismo sinttico. 
          | Sus pinturas son paisajes, retratos y bodegones, en los que se eliminaron aquellos colores que caracterizaron al impresionismo, centrndose en colores sin brillo como grises, marrones y verdes, posteriormente se recurri a otros colores.
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/Pablo-Ruiz-Picasso.png', alt='Pablo Ruiz Picasso')      
  
    TabsA.color-primario.color-custom.mb-5
      .tarjeta.color-primario.color-custom.p-4(titulo="Cubismo analtico")
        .row
          .col-xl-1
          .col-xl-4
            .p.titulo__template--a.mb-4 Fragment las masas en figuras geomtricas, representando cuerpos complejos en forma simple en un mismo plano.
          .col-xl-1
          .col-xl-6.mb-4
            figure
              img(src='@/assets/curso/Cubismo-analitico.png', alt='Cubismo analtico')
      .tarjeta.color-primario.color-custom.p-4(titulo="Cubismo sinttico")
        .row
          .col-xl-1
          .col-xl-4
            .p.titulo__template--a.mb-4 Se centr en la composicin bidimensional. En cuanto al rostro humano, este apareca con la nariz de perfil y el ojo de frente, se pierde la sensacin de profundidad, careciendo de detalles.
          .col-xl-1
          .col-xl-6.mb-4
            figure
              img(src='@/assets/curso/Cubismo-sintetico.png', alt='Cubismo sinttico')

    .row.justify-content-center.mb-5.cajon.color-acento-botones
      .col-lg-2.justify-content-center.col-5.px-lg-5.p-4
        figure
          img(src='@/assets/curso/picasso.svg', alt='Picasso')
      .col-lg-9.p-lg-4.px-4
        p El cubismo tambin se desarroll en la escultura, con tcnicas parecidas al 
          strong collage 
          | del cubismo sinttico, que integr seales y fragmentos de cosas reales. Se elabor con materiales de desecho, compuestas de diversas piezas, con huecos y vacos en la superficie. En la arquitectura el cubismo no tuvo casi representacin, se realiz sobre todo en Checoslovaquia, como en la casa de la Virgen negra de Praga.

    // Impresionismo
    h4.mb-4 Impresionismo

    .row.justify-content-center.mb-5.cajon.color-acento-botones
      .col-lg-2.justify-content-center.col-5.p-4
        figure
          img(src='@/assets/curso/pintura.svg', alt='Pintura')
      .col-lg-9.p-lg-4.px-4
        p Este movimiento artstico se desarroll a partir de la segunda mitad del siglo XIX en Europa, especialmente en Normanda. 
          strong Propenda por plasmar la luz con la impresin visual en un instante determinado, 
          | gracias a la evolucin y creacin de nuevos pigmentos, que generaron gran variedad de colores y como adems los leos venan en tubos, eso impeda que se secaran cuando pintaban al aire libre. Los pintores usaron colores puros, dando origen a la ley del contraste cromtico, ya que todo color es relativo a los colores que le rodean, y a la ley de los colores complementarios. Contrastaron la luz con la oscuridad, generando la ilusin de profundidad por medio del modelado de color, que consiste en que por medio del color se crea volumen, con una zona de luz, de media sombra y sombra ms profunda, agregando blanco para la luz y negro para la sombra. 

    .row.mb-5
      .col-lg-7
        p Para los pintores impresionistas el dibujo y la forma pasaban a un segundo plano, siendo 
          strong el reto ms importante plasmar la iluminacin y el color sobre el cuerpo o el objeto. 
          |  Por eso muchas imgenes se ven como diluidas o mezcladas. Buscaron iluminacin interior, exterior, iluminacin natural filtrada o al aire libre con los reflejos sobre el agua, o la luz del sol a travs de las hojas de los rboles. Ellos entendieron la ciudad como un paisaje. Sentan que un momento era irrepetible y que deba captarse de inmediato, igualmente comprendieron el efecto de la refraccin de la luz sobre los objetos, seleccionando ngulos imprevistos en sus pinturas. Igualmente, los momentos desapercibidos u ocultos a los espectadores, fueron captados en las pinturas y el dibujo perfectamente delineado fue eliminado, coloreando directamente sobre el lienzo; recurriendo a dos tcnicas, la primera mezclando un color sobre el otro, la segunda, colocando colores primarios uno al lado del otro para que, al ser observados, se cause la percepcin de color secundario. Igualmente, al economizar detalles, realizaron pinceladas directas, a veces brochazos o trazos gruesos. Pintaron estaciones de tren, fiestas, parques, comidas, carreras de caballos, la vida bohemia entre otros temas.
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/impresionismo.png', alt='Impresionismo')      

    // Neoclasicismo
    h4.mb-4 Neoclasicismo
      
    p.mb-4 Fue un movimiento artstico con races filosficas y estticas de la antigedad clsica, movimiento moralizante, social e intelectual, para adaptarse a la modernidad y reinterpretar sus formas, que surgi a mediados del siglo XVIII y se extendi hasta el XIX. Su furor se produjo debido al descubrimiento de Pompeya y Herculano, por lo que se realizaron numerosas expediciones para conocer las obras antiguas en sus lugares de origen. Adems, 
      strong apareci la Ilustracin o el Iluminismo, que defenda la razn y el conocimiento para fomentar el progreso; 
      | igualmente, tuvo lugar la revolucin industrial que modific los modos de produccin y organizacin social, y la revolucin francesa con sus proclamas de igualdad, libertad y fraternidad. Este movimiento se origin en Francia.
    
    .row.justify-content-center.mb-4
      .col-lg-10
        figure
          img(src='@/assets/curso/Neoclasicismo.png', alt='Neoclasicismo')  
    
    p.mb-4 Sus caractersticas fueron: propendan por la educacin y la moralizacin para construir una sociedad ms culta, ms racional y educada. El culto a la razn, los temas que destacaban las virtudes, el autodominio y la moderacin, la libertad, el herosmo y el espritu de sacrificio, se inspiraron en la antigedad clsica como un referente moral, a travs del equilibrio, la proporcin y la simetra, pero adaptada a la modernidad.

    .row.justify-content-center.mb-5.cajon.color-acento-botones
      .col-lg-2.justify-content-center.col-5.p-4
        figure
          img(src='@/assets/curso/arco-del-triunfo.svg', alt='Icono del Arco del triunfo')
      .col-lg-9.p-lg-4.px-4
        p El arte de este perodo tom como temas modelos, 
          strong ideas morales de la historia grecolatina, la historia de las repblicas y de la revolucin francesa; 
          | elaboraron retratos y algunas pinturas religiosas. En cuanto a la pintura el dibujo era de vital importancia, se us el fresco, pero primordialmente el leo; supresin de objetos decorativos, en cuanto a la anatoma se utiliz lo impuesto por los clsicos antiguos, las pinceladas no se distinguen en las obras, la luz es clara y fra. 

    .row.mb-5
      .col-lg-7
        p En cuanto a la escultura se prefiri el mrmol, el alabastro y el cobre, exaltando la belleza ideal y el equilibrio conforme a los clsicos antiguos; se dej a un lado la policroma que consista en el arte de pintar o decorar con varios colores las obras; la representacin de las emociones era sutil; las esculturas no son dinmicas.
        p En relacin con la arquitectura, el estilo neoclsico fue el preferido para realizar construcciones civiles y oficiales en el siglo XVIII y gran parte del XIX, pero tambin se ejecutaron algunas construcciones religiosas. 
          strong La arquitectura se basaba en el respeto de los planos y volmenes, 
          | el material fundamental era la piedra y se introduce el hierro, se dejan influenciar por las columnas de la arquitectura griega y las cubiertas son abovedadas como en el arte romano, la decoracin es escasa y clsica, las lneas rectas dominan sobre las curvas. En este perodo se construyen edificios como museos, teatros, bibliotecas, monumentos y cementerios pblicos.
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/arco-del-triunfo.png', alt='Foto del Arco del triunfo')     

    .super-separador.mb-5
      .separador2 





    ////////////////

    // 1.3
    #t_1_3.titulo-segundo
      .h2 1.3. Exponentes, movimientos y tendencias del diseo

    p.mb-3 Existen diversas tendencias y exponentes de cada periodo, es importante aclarar que un mismo artista puede pertenecer a varias tendencias, ya que muchas de ellas se han dado en cortos periodos de tiempo.
    
    .p.titulo__template--a.mb-4 Este auge en Grecia se produjo en tres etapas: 

    AcordionA.mb-5(tipo="b" clase-tarjeta="tarjeta tarjeta--gris")
      .row(titulo="Art nouveau")
        .col-md-6.mb-4.mb-md-0
          p Finales del siglo XIX, principios del siglo XX.
          p Este movimiento se caracteriza por tener grandes artistas en su lista, uno de sus mayores expositores es Alphonse Mucha, quien revolucion con su obra la cual 
            strong representaba la belleza y erotismo de la mujer, con gran delicadeza y sofisticacin. 
            | Alphonse Mucha salta a la fama con la invencin de un cartel para la obra Gismonda protagonizada por la actriz Sarah Bernhardt, actriz de cine y teatro muy importante en la movida parisina. Sarah fue la musa de varios de sus trabajos.
          p Este movimiento nace bajo las formas poticas de la naturaleza, el manejo enriquecido de la lnea, las curvas, fuerza y movimiento que pueden aportar, adaptndose a la vida moderna. Al igual que muchos otros movimientos el Art nouveau, imprime su sello en todas las reas artsticas de la poca.
          p.mb-5 Dentro de los exponentes ms importantes estn, Aubrey Beardsley, Will Bradley, Antoni Gaud, Toulouse Lautrec y Gustav Klimt.  
          strong Caractersticas:
          ul.lista.lista-ul--color.p-1.mt-2
            li 
              i.fas.fa-circle
              p.mb-0 nfasis en elementos de la naturaleza, un estilo joven y moderno.
            li 
              i.fas.fa-circle
              p.mb-0 Manejo de la lnea y las formas naturales.
            li 
              i.fas.fa-circle
              p.mb-0 Es el primer movimiento que trata de no parecerse a otro.
            li 
              i.fas.fa-circle
              p.mb-0 Se utilizaron gran cantidad de materiales, sobre todo en la arquitectura y el diseo industrial.
            li 
              i.fas.fa-circle
              p.mb-0 nfasis rico en el color.    
        .col-md-6.px-md-5.p-3
          figure
            img(src='@/assets/curso/Art-nouveau.png', alt='Gismonda de Alfons Mucha.')

      div.row(titulo="Constructivismo")
        .col-md-6.mb-4.mb-md-0
          p (1914) Rusia.
          p El constructivismo nace en Rusia en 1914, despus de la 
            strong revolucin de octubre, 
            | e influencia el arte, la escultura, el diseo y la arquitectura; apoyado en las bases del cubismo, surge en la coyuntura poltica del pas. 
          p En el ao 1917, Malevich emple el trmino construction art (en espaol arte para construccin), haciendo referencia al trabajo de Rdchenko, bajo una connotacin negativa.
          p Una de las obras ms icnicas de este periodo es realizada por Vladimir Tatlin, pintor y escultor al cual se asocia con el nacimiento del constructivismo, dise el Monumento a la tercera internacional o tambin llamada o torre de Tatlin, es un edificio habitable, ms alto que la propia torre Eiffel, el cual no se edific.
          p.mb-5 Entre sus principales exponentes adems de Vladimir Tatlin, estn: Aleksandr Rdchenko, Aleksandra Ekster, Jnos Mttis-Teutsch, Lajos Kassk, El Lissitzky, entre otros.
          strong Caractersticas:
          ul.lista.lista-ul--color.p-1.mt-2
            li 
              i.fas.fa-circle
              p.mb-0 Se basa en la abstraccin geomtrica y funcional.
            li 
              i.fas.fa-circle
              p.mb-0 Sus fuentes de inspiracin son el cubismo y el dadasmo.
            li 
              i.fas.fa-circle
              p.mb-0 Se utilizan colores como el rojo, naranja, azul, blanco, negro y amarillo. Rechaza el arte burgus de la poca. 
            li 
              i.fas.fa-circle
              p.mb-0 Se asocia a la produccin industrial.
            li 
              i.fas.fa-circle
              p.mb-0 Se utilizan tipografas en negritas en los carteles sobre todo en la propaganda poltica.
        .col-md-6.px-md-5.p-3
          figure
            img(src='@/assets/curso/Constructivismo.svg', alt='Afiche constructivista, simboliza la unidad y solidaridad con la gente oprimida.')

      div.row(titulo="Bauhaus")
        .col-md-6.mb-4.mb-md-0
          p Casa de la construccin estatal, 1919 Alemania.
          p La casa de la construccin estatal o la Bauhaus, fue fundada en 1919 por Walter Gropius en Weimar, Alemania; basa sus propuestas y declaraciones en la idea de una necesaria 
            strong reforma de la enseanza artstica, como base para la transformacin de la sociedad burguesa de la poca, 
            | de acuerdo con el pensamiento socialista de su fundador. La primera fase (1919-1923) fue idealista y romntica; la segunda (1923-1925), mucho ms racionalista, y en la tercera (1925-1929) alcanz su mayor reconocimiento, coincidiendo con su traslado de la ciudad de Weimar a Dessau (Diez y Parra, 2015).
          p Su caracterstica principal de diseo es la sustraccin de ornamentos como los utilizados en el Art nouveau, de formas limpias y geomtricas, colores primarios que buscan la funcionalidad con la menor cantidad de elementos. Esta escuela fue cerrada por el partido nacional socialista bajo el mandato de Hitler.
          p.mb-5 Sus principales exponentes son: Walter Gropius, Paul Josef Albers, Lyonel Feininger, Alfred Arndt, Hebert Bayer, Ludwig Hilberseimer, Marianne Brandt, Johannes Itten, Marcel Breuer, Wassily Kandinsky.  
          strong Caractersticas:
          ul.lista.lista-ul--color.p-1.mt-2
            li 
              i.fas.fa-circle
              p.mb-0 Su principal funcin es la innovacin en el pensamiento, dejando atrs los convencionalismos del momento, la forma sigue a la funcin. 
            li 
              i.fas.fa-circle
              p.mb-0 La Bauhaus hizo aportes importantes al diseo grfico e industrial que se utilizan hasta el da de hoy, basados en las tendencias modernistas del uso y relacin entre el diseo, materiales funcionales y minimalistas. 
            li 
              i.fas.fa-circle
              p.mb-0 El diseo tipogrfico se enriquece con el aporte de nuevos estilos, que apuestan por la funcionalidad ms que por la esttica.  
            li 
              i.fas.fa-circle
              p.mb-0 Los colores predominantes son los primarios, amarillo, azul y rojo en contraste con formas bsicas como el tringulo, el crculo y el cuadrado. 
            li 
              i.fas.fa-circle
              p.mb-0 Las formas geomtricas y abstractas se utilizan para simplificar los elementos. 
        .col-md-6.px-md-5.p-3
          figure
            img(src='@/assets/curso/Bauhaus.png', alt='Patrn de bauhaus.')

      div.row(titulo="Art dco")
        .col-md-6.mb-4.mb-md-0
          p Europa 1920  1939
          p A la par que en Alemania se desarrolla la escuela de la Bauhaus, Francia y otros pases adoptan un movimiento diferente, llamado Art dco, movimiento modernista, que se sita con una 
            strong visin influenciada por diversas estticas 
            | como la del constructivismo ruso, del cual toma las lneas duras y la solidez de las formas; El cubismo y el Futurismo son movimientos tambin presentes en su tendencia, uso de lneas aerodinmicas, fraccionamiento y repeticin de formas, y geometrizacin de estas.
          p Materiales como aluminio, acero inoxidable, piel de animales, maderas y otros, son los mayormente utilizados. En arquitectura uno de los mejores ejemplos de este estilo es la cpula del edificio Chrysler en la ciudad de Nueva York; construido entre 1928 y 1930, en este se observan el uso de las lneas, la repeticin, y formas caractersticas del movimiento, los diversos materiales enfocados hacia un concepto industrializado.
          p.mb-5 Esta corriente busca un nuevo optimismo despus de vivir la primera guerra mundial, 
            strong sus diseos imprimen un toque elegante y suntuoso, encaminado hacia un futuro esperanzador. 
            | Los colores son vibrantes e irradian mucho brillo, esto como influencia del Fauvismo, las tipografas realizadas en el Art dco, eran revestidas en muchos casos por una capa de oro, lo cual imprima un toque de suntuosidad.  
          strong Caractersticas:
          ul.lista.lista-ul--color.p-1.mt-2
            li 
              i.fas.fa-circle
              p.mb-0 Es un movimiento posguerra, basado en el lujo y la suntuosidad, que desea dejar atrs las carencias de la poca. 
            li 
              i.fas.fa-circle
              p.mb-0 Las piezas de este movimiento son puramente decorativas, en la mayora de los casos.
            li 
              i.fas.fa-circle
              p.mb-0 Gira alrededor del progreso, el urbanismo, la ciudad y la maquinaria. 
            li 
              i.fas.fa-circle
              p.mb-0 Uso continuo de colores, brillos, lneas y formas aerodinmicas.
        .col-md-6.px-md-5.p-3
          figure
            img(src='@/assets/curso/Art-deco.png', alt='Edificio Chrysler en la ciudad de Nueva York.')            

      div.row(titulo="El diseo grfico en los aos 30")
        .col-md-6.mb-4.mb-md-0
          p La dcada de los aos 30 se caracteriz en 
            strong dejar atrs los excesos del Art dco, 
            | esto sumado a la gran depresin de 1928, gener un cambio en el diseo y las artes. Con formas restringidas tomadas del Art dec y colores tenues, el cartel se vuelve sombro. 
          p Tschichold, tipgrafo alemn, hace grandes aportes a la tipografa, el arte y la arquitectura, con la aplicacin de conceptos constructivistas. En su libro La nueva tipografa, da a conocer tipografas asimtricas y conceptos del diseo asimtrico, que poco se utilizaba en ese momento. 
          p.mb-5 Los avances tecnolgicos y el diseo van a la par, esto se refleja en los trabajos realizados, que buscan dar un falso movimiento a las piezas. Se destacan en esta dcada: Herbert Matter, Piet Zwart, y Lester Beall.
          strong Caractersticas:
          ul.lista.lista-ul--color.p-1.mt-2
            li 
              i.fas.fa-circle
              p.mb-0 Diseos sobrios.
            li 
              i.fas.fa-circle
              p.mb-0 Colores tenues, poco clidos. 
            li 
              i.fas.fa-circle
              p.mb-0 Formas no simtricas. 
        .col-md-6.px-md-5.p-3
          figure
            img(src='@/assets/curso/diseno-grafico-anos-30.png', alt='Cartel Art dco. Ilustrador Cassandre.')   

      div.row(titulo="El diseo durante la II guerra mundial (cartel blico)")
        .col-md-6.mb-4.mb-md-0
          p En 1939 comienza la segunda guerra mundial, conflicto que durar hasta el ao 1945, dividiendo al mundo entre el bloque occidental y el comunista.  La nacin de la guerra y las revoluciones, hacen que el diseo sea un arma poltica, capaz de transmitir un mensaje claro y efectivo, ya que es utilizado por los Gobiernos para impulsar sus ideologas, infundir miedo y como propaganda poltica. Los pases que ms sacaron partido del diseo blico durante esta poca fueron Alemania y Estados Unidos. 
            strong El cartel se vuelve una pieza fundamental de convocatoria masiva para reclutar gente, fondos y desprestigiar al bando contrario. 
            | Debe ser expresivo, pues la imagen llega a mucha gente analfabeta.
          p Se utilizan colores de gama grisceas y en otros colores primarios, sobre todo para exaltar al Gobierno y el servicio a la patria, al cual estaban destinados los hombres.
          p.mb-5 En Estados Unidos sobresale el trabajo de James Montgomery Flagg, del famoso cartel del to Sam; J.Howard Miller, creado del cartel Podemos hacerlo!, que se muestra en la imagen; y en Alemania Joseoph Goeblels, con un cartel icnico citando la victoria ser nuestra, el cual muestra un soldado frente a la bandera del partido Nazi.
          strong Caractersticas:
          ul.lista.lista-ul--color.p-1.mt-2
            li 
              i.fas.fa-circle
              p.mb-0 Uso poltico.
            li 
              i.fas.fa-circle
              p.mb-0 Se utiliza la manipulacin mediante el cartel.
            li 
              i.fas.fa-circle
              p.mb-0 Utilizacin de colores primarios y neutros. 
        .col-md-6.px-md-5.p-3
          figure
            img(src='@/assets/curso/cartel-belico.png', alt='Afiche Podemos hacerlo!, creado por J. Howard Miller, en 1942, durante la segunda guerra mundial; impulsa la moral de las mujeres estadounidenses que contribuyen a los esfuerzos de guerra.')               

    .super-separador.mb-5
      .separador2 



    ////////////

    // 1.4
    #t_1_4.titulo-segundo
      .h2 1.4. Diseo bsico

    figure.mb-5
      img(src='@/assets/curso/banner-tema4.jpg', alt='Diseo bsico')   
    p.mb-4.texto-resaltado El diseo visual, base de la creacin del diseo, es considerado hoy un eje fundamental de la vida cotidiana de los individuos; puesto que vivimos en un mundo cada vez ms globalizado, donde estamos saturados por miles de imgenes que a diario se distribuyen a travs de diversos medios. El lenguaje como definicin se puede entender como la capacidad exclusiva del ser humano para expresar pensamientos y sentimientos por medio de la palabra. Es as como nos comunicamos por medio del lenguaje escrito, verbal y visual, cada uno con un cdigo en particular, utilizados en diversos campos y mbitos de la vida cotidiana.  Si nos referimos al 
      span.etiqueta.text-bold lenguaje visual,
      | podemos decir entonces, que 
      span.etiqueta.text-bold es la capacidad de comunicarnos mediante imgenes,
      | utilizadas como medio de expresin en todas sus formas y posibilidades, 
      span.etiqueta.text-bold para dar a conocer sensaciones, pensamientos, emociones y sentimientos,
      | que se pueden expresar en representaciones grficas.

    .row.mb-5
      .col-lg-7
        p Segn el diccionario etimolgico (2020), la palabra imagen proviene del latn imago, la cual significa retrato, copia o imitacin. A pesar de esto la imagen a diferencia de la escritura o el lenguaje, suele tener un grado de dificultad en cuanto a su lectura, ya que cada individuo puede realizar su propia interpretacin de la misma, lo cual no sucede con las palabras, por ejemplo; al decir perro, todos inmediatamente hacemos la asociacin con un animal de 4 patas, indiferente de su raza. 
        p No siempre una imagen vale ms que mil palabras; 
          strong por ejemplo, esta seal significa circulacin prohibida, es probable que pocos asocien la imagen a este concepto.
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/diseno-basico.png', alt='Seal de trnsito vaca')  
    


    // 1.4.1
    .h3.mb-5 Herramientas del lenguaje visual

    .p.titulo__template--a.mb-5 El lenguaje visual se organiza a partir de dos tipos de herramientas:  

    h4 a) Herramienta de configuracin 
    p.mb-4 Dentro de las herramientas de configuracin podemos resaltar las siguientes: tamao, forma, color, textura e iluminacin.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10.align-items-center 
        .row.align-items-center 
          .col-lg-6
            .tarjeta-avatar-b.tarjeta-iconos.mb-3
              img(src='@/assets/curso/image-tamano.svg' alt='Tamao de imagen')
              .tarjeta.color-naranja
                .p-4
                  .h3 Tamao
                  p Hace referencia a la dimensin adecuada con la cual trabajar una imagen de acuerdo con los criterios requeridos, para conseguir los mejores resultados. 
            .tarjeta-avatar-b.tarjeta-iconos.mb-3
              img(src='@/assets/curso/formas.svg' alt='Formas geometricas')
              .tarjeta.color-naranja
                .p-4
                  .h3 Forma
                  p Todo aquellos que nos rodea tiene una forma, la podramos definir como el aspecto exterior de las cosas, que presentan un contorno, textura, tamao y color. 
            .tarjeta-avatar-b.tarjeta-iconos.mb-3
              img(src='@/assets/curso/suelo.svg' alt='Textura')
              .tarjeta.color-naranja
                .p-4
                  .h3 Textura
                  p Toda figura tiene una superficie, esta puede tener diferentes caractersticas, unas suaves, otras speras, arrugadas, lisas o abultadas. Existen la textura visual y la textura tctil que sern abordadas ms adelante. 

          .col-lg-6
            .tarjeta-avatar-b.tarjeta-iconos.mb-5
              img(src='@/assets/curso/rueda-de-color.svg' alt='Rueda de color')
              .tarjeta.color-naranja
                .p-4
                  .h3 Color
                  p El color lo podemos definir como la impresin que producen en la retina los rayos de luz reflejados y absorbidos por un cuerpo, segn la longitud de onda de estos (Lxico, s.f.).  El lenguaje visual tiene como gran recurso la utilizacin del color, el cual puede aportar mucha fuerza, carcter e informacin que enriquece la imagen. Mediante el color se pueden afianzar los conceptos, fro, calor, primavera, invierno, entre otros, de igual manera ofrece una gama ilimitada de posibilidades en su tratamiento. 


    h4 b) Herramientas de organizacin 
    p.mb-4 Dentro de las herramientas de organizacin encontramos: la composicin, y la retrica visual.

    .row.justify-content-center.align-items-center
      .col-lg-10.align-items-center
        .tarjeta-avatar-b.tarjeta-iconos2.mb-4
              img(src='@/assets/curso/disenador.svg' alt='Icono sobre diseador')
              .tarjeta.color-naranja
                .p-4
                  .h3 La composicin
                  p La composicin de una imagen se hace ordenando las herramientas y elementos que integran la obra en pro de la intencionalidad y mensaje a transmitir.  
      
      .col-lg-10.align-items-center.mb-5
        .tarjeta-avatar-b.tarjeta-iconos2
          img(src='@/assets/curso/discurso-de-burbuja.svg' alt='Icono sobre discurso de burbuja')
          .tarjeta.color-naranja
            .p-4
              .h3 La retrica visual
              p Reconecta los significados de los componentes del producto visual, para transmitir mensajes y sensaciones ms all del sentido literal.  Los tres componentes de la retrica visual son:
              ul.lista.lista-ul--color.p-1.mt-2.lista2  
                li 
                  i.fas.fa-atom
                  p.mb-0 
                    strong Metfora: 
                    | comparacin implcita de dos trminos a partir de la semejanza de sus significados estableciendo una relacin. Ejemplo, el sueo eterno, para referirse a la muerte.
                li 
                  i.fas.fa-atom
                  p.mb-0 
                    strong Alegora: 
                    | metfora extendida o sucesin de metforas para expresar algo diferente a lo dicho. Ejemplo, ella gira dentro de mi cabeza.
                li 
                  i.fas.fa-atom
                  p.mb-0 
                    strong Metonimia: 
                    |  reemplazar el nombre de un elemento por otro con el que se relaciona, o existe una relacin.                  



    // 1.4.2
    .h3 Gramtica del lenguaje visual

    .row.mb-5
      .col-lg-7
        p As como el lenguaje escrito necesita de un abecedario, signos y smbolos para poder llegar a manifestar de forma asertiva un mensaje; el lenguaje visual tambin necesita de ciertos recursos grficos para comunicar de manera correcta aquello que desea expresar, ordenando para ellos los elementos que hacen parte de la comunicacin grfica, desde el punto y la lnea. No obstante, no podemos negar que el proceso visual es una sntesis de conocimientos perceptivos que acompaan al individuo, por lo que su lectura respecto a la imagen es individual.  
      .col-lg-5
        h3 En resumen, podemos decir que la gramtica visual, ordena los elementos que hacen parte de s misma, los procesos que lleva a cabo y las relaciones que se producen.

    h4 Forma, repeticin, estructura, contraste y textura
    p.mb-4 Cuando hablamos de elementos del diseo, podemos decir que existen elementos conceptuales y elementos visuales. 

    .row.columnas-iguales.justify-content-md-center
      .col-lg-6.justify-content-md-center.mb-5.p-5.tarjeta.color-naranja.columna-left
        figure
          img.p-2.mb-4(src='@/assets/curso/cubo-3d.svg', alt='Cubo 3D')
        p.text-center.mb-4 Al referirnos a los 
          strong elementos conceptuales, 
          | nos referimos a aquellos elementos que, aunque no son visibles, estn en nuestra mente por asociacin; por ejemplo, cuando dibujamos un cubo por medio de lneas, implcitamente le damos una tridimensionalidad que no es real, pero que se forma como reflejo de la imagen y la asociacin.
      .col-lg-6.justify-content-md-center.mb-5.p-5.tarjeta.color-celeste.columna-right
        figure
          img.p-2.mb-4(src='@/assets/curso/cubos-de-azucar.svg', alt='Cubos de azucar')
        p.text-center.mb-4 Por otro lado, estn los 
          strong elementos visuales 
          | que hacen destacar las caractersticas de una imagen, como su color, textura, ancho y alto. Un concepto muy utilizado dentro del diseo es el 
          strong mdulo, 
          | elemento tomado como unidad de medida, utilizada dentro de una composicin grfica, la cual suele repetirse sistemticamente.

    // Forma
    h4 Forma 

    .row.mb-3
      .col-lg-7
        p Como fuera sealado, los elementos conceptuales no son visibles, as el punto, la lnea o el plano, cuando son visibles pasan a ser una forma. Un punto sobre el papel por pequeo que sea debe tener una figura, un tamao, un color y una textura si se pretende que sea visto. Tambin debe sealarse lo mismo de una lnea o de un plano. En un diseo bidimensional, el volumen es imaginario.
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/forma.png', alt='Forma')      

    TabsC.color-primario.mb-5
      .py-3.py-md-4(titulo="La forma como punto")
        .row.justify-content-center
          .col-md-8
            .h3 La forma como punto
            p.mb-5 Una forma es reconocida como un punto porque es pequea. El tamao desde luego es relativo. Una forma puede parecer bastante grande cuando est contenida dentro de un marco pequeo, pero la misma forma puede parecer muy pequea si se coloca dentro de un marco mucho ms amplio.
            .row.justify-content-center
              .col-md-10
                figure
                  img(src='@/assets/curso/forma-como-punto.svg', alt='La forma como punto')
      .py-3.py-md-4(titulo="La forma como lnea")
        .row.justify-content-center
          .col-md-8
            .h3 La forma como lnea
            p Una forma es reconocida como una lnea por dos razones:
            ol.lista-ol--cuadro
              li 
                .lista-ol--cuadro__vineta
                  span 1
                | Su ancho es extremadamente reducido.
              li 
                .lista-ol--cuadro__vineta
                  span 2
                | Su longitud es sobresaliente.
            p.mb-5 Una lnea por lo general transmite la sensacin de delgadez. La delgadez igual que el tamao es relativo.
            .row.justify-content-center  
              .col-md-10
                figure
                  img(src='@/assets/curso/forma-como-linea.svg', alt='La forma como lnea')
      .py-3.py-md-4(titulo="La forma como plano")
        .row.justify-content-center
          .col-md-8
            .h3 La forma como plano
            p.mb-5 Es una superficie bidimensional, todas las formas lisas que comnmente no sean reconocidas como puntos o lneas son planos. Una forma plana es limitada por lneas conceptuales que constituyen los bordes de la forma. Las caractersticas de estas lneas conceptuales y sus interrelaciones determinan la figura de la forma plana (Diseo grfico, s.f.).
            .row.mb-4
              .col-md-5
                p.mb-3
                  strong a.  Geomtricas
                figure
                  img(src='@/assets/curso/formas-geometricas.svg', alt='Formas Geomtricas')
              .col-md-2    
              .col-md-5
                p.mb-3 
                  strong b. Orgnicas
                figure
                  img(src='@/assets/curso/formas-organicas.svg', alt='Formas Orgnicas')
            .row
              .col-md-5
                p.mb-3
                  strong c.  Rectilneas
                figure
                  img(src='@/assets/curso/formas-rectilineas.svg', alt='Formas Rectilneas')
              .col-md-2    
              .col-md-5
                p.mb-3 
                  strong d.  Irregulares  
                figure
                  img(src='@/assets/curso/formas-irregulares.svg', alt='Formas Irregulares')      

    // Repeticin
    h4 Repeticin 

    p.mb-4 La repeticin 
      strong tiene como funcin unificar el diseo, 
      | se da como resultado a partir de la repeticin continua de un mismo elemento dentro de un espacio, lo cual permite igualdad, ritmo y equilibrio a nivel visual.

    .row.justify-content-center.mb-5
      .col-lg-10
        figure
          img(src='@/assets/curso/repeticion.png', alt='Repeticin')

    // Estructura
    h4 Estructura 

    p.mb-4 La estructura 
      strong sugiere un orden 
      | y predefine las relaciones internas de las formas en un diseo, es decir la posicin. Podemos haber creado un diseo sin haber pensado conscientemente en la estructura, pero la estructura est siempre presente cuando hay una organizacin (Wong, 1991). 
    .row.justify-content-center.mb-5
      .col-lg-10
        figure
          img(src='@/assets/curso/Estructura.png', alt='Estructura')

    // Radiacin
    h4 Radiacin 

    p.mb-4 Puede ser descrita como  
      strong un caso especial de repeticin. 
      | Los mdulos repetidos o las subdivisiones estructurales que giran regularmente alrededor de un centro comn produciendo un efecto de radiacin. La radiacin es un fenmeno comn en la naturaleza; por ejemplo, lanzar una piedra a un lago genera ondas concntricas, provocando una gradacin, serie ordenada gradualmente (Godoy, 2009).
    .row.justify-content-center.mb-5
      .col-lg-10
        figure
          img(src='@/assets/curso/Radiacion.png', alt='Radiacin')

    // Contraste
    h4 Contraste 

    p El contraste ocurre siempre, aunque su presencia pueda no ser advertida. Se presenta el contraste cuando una forma est rodeada de un espacio blanco; cuando una lnea recta se cruza con una curva; cuando una forma es mucho mayor que otra; cuando coexisten direcciones verticales y horizontales. El contraste 
      strong es solo una clase de comparacin, por la cual las diferencias se hacen claras.
    p.mb-4 Dos formas pueden ser similares en algunos aspectos y diferentes en los otros. Sus diferencias quedan enfatizadas cuando hay un contraste. Una forma puede no parecer grande si es vista por s sola, pero puede parecer gigantesca junto a formas diminutas.

    .row.justify-content-center.mb-4
      .col-lg-10.tarjeta.color-naranja.p-5.pb-0
        SlyderC(:datos="datosSlyder")

    p.mb-5 La regularidad no produce necesariamente un buen diseo, aunque puede garantizar cierto grado de armona. El mismo grupo de mdulos utilizados en una estructura de repeticin, pueden derivar a un diseo opaco en las manos de un diseador y a un diseo llamativo en manos de otro. El debido uso del contraste en los elementos de relacin puede explicar esa diferencia.  El contraste puede ser derivado de elementos como la figura, el tamao, el color, la textura, la direccin de los elementos, la posicin, el espacio y la gravedad (Godoy, 2009).     

    // Textura
    h4 Textura 

    .row.mb-3
      .col-lg-7
        p De acuerdo con Wong (1991), la textura 
          strong se refiere a las caractersticas de una superficie de una figura. 
          | Toda figura tiene una superficie y toda superficie debe tener ciertas caractersticas, que pueden ser descritas como suave, rugosa, lisa o decorada, opaca o brillante, blanda o dura. Aunque generalmente suponemos que una superficie plana y pintada no tiene textura alguna, en realidad la capa de pintura es ya una textura; como tambin, la textura del material sobre el que fue creada la figura; la textura apropiada aade riqueza a un diseo. 
      .col-lg-5
        figure.mb-4
          img(src='@/assets/curso/Textura.png', alt='Textura')    

    .p.titulo__template--a.mb-4 La textura puede ser clasificada en dos importantes categoras; 
      strong textura visual y textura tctil.

    ol.lista-ol--cuadro
      li 
        .lista-ol--cuadro__vineta
          span a
        strong Textura visual  

    p La textura visual es bidimensional, se puede apreciar a travs de la vista, aunque puede evocar tambin sensaciones tctiles. Se distinguen tres clases de textura visual:

    .row.mb-5
      .col-lg-4
        figure.mb-4
          img(src='@/assets/curso/Textura-decorativa.png', alt='Textura decorativa')
        p.mb-3 
          strong Textura decorativa
        p Decora una superficie y queda subordinada a la figura, en otras palabras, la textura misma es solo un agregado que puede ser quitado sin afectar mucho a las figuras.
      .col-lg-4
        figure.mb-4
          img(src='@/assets/curso/Textura-espontanea.png', alt='Textura espontnea')
        p.mb-3 
          strong Textura espontnea
        p No decora una superficie, sino que es parte del proceso de creacin visual. La figura y la textura no pueden ser separadas, porque las marcas de la textura en una superficie son al mismo tiempo las figuras. Las formas dibujadas a mano y las accidentales, contienen frecuentemente una textura espontnea. 
      .col-lg-4
        figure.mb-4
          img(src='@/assets/curso/Textura-mecanica.png', alt='Textura mecnica')
        p.mb-3 
          strong Textura mecnica
        p Textura obtenida por medios mecnicos especiales y en consecuencia la textura no est necesariamente subordinada a la figura. Un ejemplo es el granulado fotogrfico o la retcula que encontramos a menudo en los impresos. La textura mecnica puede encontrarse asimismo en los diseos creados por la tipografa y en los grficos de computadoras. 


    ol.lista-ol--cuadro
      li 
        .lista-ol--cuadro__vineta
          span b
        strong Textura tctil  

    p.mb-5 Se percibe a travs del tacto, generando una sensacin que puede describirse como una superficie lisa o con relieve; la textura puede ser propia del material, modificada cuando se ha agregado una textura adicional a la natural; y organizada cuando se unen o sobreponen materiales generando una nueva textura.

    h4.mb-4 Composicin, percepcin y equilibrio visual

    .row.justify-content-center.mb-4.cajon.color-acento-botones
      .col-lg-2.justify-content-center.col-5.px-md-5.p-4
        figure
          img(src='@/assets/curso/idea.svg', alt='Icono sobre idea')
      .col-lg-9.p-lg-4.px-4
        p La 
          strong composicin 
          | en diseo 
          strong es el ordenamiento y disposicin de los distintos elementos dentro del espacio visual de manera ordenada, con el fin de transmitir un mensaje objetivo al pblico. 
          | El xito de cualquier proyecto grfico reside en que la composicin sea clara, concisa, sencilla y directa, esto posibilita una mejor lectura de esta. La composicin consiste en jugar con los elementos, rotarlos, cambiarlos de posicin, ubicarlos por grupos, por colores, etc. 

    .p.titulo__template--a.mb-4 Existen algunas reglas que pueden ser de gran ayuda a la hora de emprender cualquier proyecto:

    .row.justify-content-center.mb-3
      .col-lg-8.tarjeta.color-naranja.p-4.mb-4
        .row.justify-content-center 
          .col-lg-4
            p Elementos ubicados a la derecha o en la parte de la margen inferior, tiene mayor fuerza a nivel visual.
          .col-lg-6
            figure
              img(src='@/assets/curso/elementos-1.png', alt='Elementos ubicados a la derecha y en la parte de la margen inferior')
      .col-lg-8.tarjeta.color-naranja.p-4.mb-4
        .row.justify-content-center 
          .col-lg-4
            p Formas largas y angulares amplan el campo de visin.
          .col-lg-6
            figure
              img(src='@/assets/curso/elementos-2.png', alt='Formas largas y angulares')
      .col-lg-8.tarjeta.color-naranja.p-4.mb-4
        .row.justify-content-center 
          .col-lg-4
            p Las formas simtricas, sencillas y regulares son ms fciles de recordar.
          .col-lg-6
            figure
              img(src='@/assets/curso/elementos-3.png', alt='Formas simtricas, sencillas y regulares')              
      .col-lg-8.tarjeta.color-naranja.p-4.mb-4
        .row.justify-content-center 
          .col-lg-4
            p Las imgenes de gran tamao producen el efecto de fortaleza, lo contrario sucede con las imgenes pequeas.
          .col-lg-6
            figure
              img(src='@/assets/curso/elementos-4.png', alt='Formas de diferentes tamaos')
      .col-lg-8.tarjeta.color-naranja.p-4.mb-3
        .row.justify-content-center 
          .col-lg-4
            p Los colores vibrantes tienen un mayor efecto visual.
          .col-lg-6
            figure
              img(src='@/assets/curso/elementos-5.png', alt='Formas de colores diferentes')

    .row.justify-content-center.cajon.color-acento-botones.mb-5
      .col-lg-2.justify-content-center.col-5.p-4
        figure
          img(src='@/assets/curso/percepcion.svg', alt='Icono sobre percepcin')
      .col-lg-9.p-lg-4.px-4
        p La 
          strong percepcin 
          | la podemos definir como la 
